import { getCookie, setCookie } from "./cookies"
import { parseJwt } from "./jwt"
import authService from "./authService";

const apiBaseUrl = import.meta.env.VITE_BASE_API_URL;

/**
 *     (5     )
 * @returns {boolean}
 */
const isTokenExpiringSoon = () => {
    const token = getCookie("accessToken");
    if (!token) return true; // TODO:        (          )

    try {
        const decodedToken = parseJwt(token);
        const expirationTime = decodedToken.exp * 1000;
        const currentTime = Date.now();
        const timeRemaining = expirationTime - currentTime;

        //  5   true 
        return timeRemaining <= 300000;
    } catch (error) {
        console.error("Error parsing JWT token:", error);
        return false;
    }
};

/**
 *  
 * @returns {boolean}    
 */
export const refreshAccessToken = async () => {
    const refreshToken = getCookie("refreshToken");
    const sessionId = getCookie("sessionId");

    if (!refreshToken || !sessionId) {
        return false;
    }

    try {
        const response = await fetch(`${apiBaseUrl}/auth/refreshToken`, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                "X-Session-Id": sessionId,
                "X-Refresh-Token": refreshToken,
            }
        });

        if (!response.ok) {
            console.error("Failed to refresh access token:", response.statusText);
            return false;
        }

        const data = await response.json();

        //   
        authService.setAuthCookies(data);
        // TODO:  RefreshToken SessionId   
        return true;
    } catch (error) {
        console.error("Error refreshing access token:", error);
        return false;
    }
};

/**
 *    fetch 
 * @param {string} endpoind - API 
 * @param {Object} options - fetch 
 * @param {boolean} requireAuth -   
 */
export const fetchWithAuth = async (endpoind, options = {}, requireAuth = true) => {
    const url = endpoind.startsWith("http") ? endpoind : `${apiBaseUrl}${endpoind}`;

    const fetchOptions = {
        ...options,
        headers: {
            "Content-Type": "application/json",
            ...(options.headers || {}),
        }
    };

    if (requireAuth) {
        if (isTokenExpiringSoon()) {
            const refreshed = await refreshAccessToken();

            //     
            if (!refreshed) {
                //       
                authService.handleTokenExpiration();
                throw new Error("Failed to refresh access token");
            }
        }

        //    
        const accessToken = getCookie("accessToken");
        if (accessToken) {
            fetchOptions.headers["Authorization"] = `Bearer ${accessToken}`;
        }
    }

    try {
        const response = await fetch(url, fetchOptions);

        // 401 Unauthorized  
        if (response.status === 401) {
            //   
            const refreshed = await refreshAccessToken();

            if (refreshed) {
                //    
                const accessToken = getCookie("accessToken");
                fetchOptions.headers["Authorization"] = `Bearer ${accessToken}`;
                return fetch(url, fetchOptions); //      fetch 
            } else {
                //    
                authService.handleTokenExpiration();
                throw new Error("Failed to refresh access token");
            }
        }

        //    
        if (!response.ok) {
            throw new Error(`API Error: ${response.status}`);
        }

        return response;
    } catch (error) {
        console.error("API Error:", error);
        throw error;
    }
};

//////////////////////////////////////////
// API   
//////////////////////////////////////////

export const api = {
    /**
     * GET 
     * @param {string} endpoint - API 
     * @param {boolean} requireAuth -   
     * @returns {Promise}
     */
    get: (endpoint, requireAuth = true) => {
        return fetchWithAuth(endpoint, { method: "GET" }, requireAuth);
    },

    /**
     * POST 
     * @param {string} endpoint - API 
     * @param {Object} options -   /   { headers: { "Content-Type": "application/json" } }  
     * @param {boolean} requireAuth -   
     * @returns {Promise}
     */
    post: (endpoint, options, requireAuth = true) => {
        return fetchWithAuth(endpoint, {
            ...options,
            method: "POST"
        }, requireAuth);
    },

    /**
     * PUT 
     * @param {string} endpoint - API 
     * @param {Object} options -   /   { headers: { "Content-Type": "application/json" } }  
     * @param {boolean} requireAuth -   
     * @returns {Promise}
     */
    put: (endpoint, options, requireAuth = true) => {
        return fetchWithAuth(endpoint, {
            ...options,
            method: "PUT"
        }, requireAuth);
    },

    /**
     * DELETE  (  )
     * @param {string} endpoint - API 
     * @param {Object} options -   /   { headers: { "Content-Type": "application/json" } }  
     * @returns {Promise}
     */
    delete: (endpoint, options) => {
        return fetchWithAuth(endpoint, {
            ...options,
            method: "DELETE"
        });
    }
}

//////////////////////////////////////////
//   API  
//////////////////////////////////////////

/**
 * S1    API 
 * @param {string} id
 * @param {string} password
 * @param {boolean} rememberMe
 * @returns {Promise}   
 */
export async function S1LoginApi(id, password, rememberMe = false) {
    const response = await fetch(`${apiBaseUrl}/Login/S1Auth/Login`, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify({ id, password, rememberMe })
    });
    return response.json();
}

/**
 * S1   API 
 * @param {string} id
 * @param {string} password
 * @returns {Promise}   
 */
export async function S1GuestLoginApi(id, password) {
    // S1    ID|PASSWORD  
    const response = await fetch(`${apiBaseUrl}/Login/S1Auth/GuestLogin`, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify({ id: `${id}|${password}`, password, rememberMe: false })
    });
    return response.json();
}

/**
 *  API 
 * @param {string} sessionId
 * @param {string} refreshToken
 * @returns {Promise}  
 */
export async function LogoutApi(sessionId, refreshToken) {
    const response = await fetch(`${apiBaseUrl}/auth/logout`, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify({ sessionId, refreshToken })
    });
    return response;
}