/**
 *     
 */
class ReceiverInfo {
    /**
     *   
     * @param {string} options.phone -  
     * @param {string} options.name -  
     * @param {Object} options.variables -   
     * @param {Object} options.queryVariables -   
     */
    constructor({
        phone = '',
        name = '',
        variables = {},
        queryVariables = {}
    } = {}) {
        /**
         *  
         * @type {string}
         */
        this.phone = phone;

        /**
         *  
         * @type {string}
         */
        this.name = name;

        /**
         *    (     "" : "")
         * @type {Object}
         */
        this.variables = variables;

        /**
         *    (     "" : "")
         * @type {Object}
         */
        this.queryVariables = queryVariables;
    }

    /**
     * JSON   
     * @returns {Object} -  
     */
    toJSON() {
        return {
            phone: this.phone,
            name: this.name,
            variables: this.variables,
            queryVariables: this.queryVariables
        };
    }
}

export default ReceiverInfo; 