/**
 *    
 */

/**
 *   
 * @param {string} name -  
 * @param {string} value -  
 * @param {Object} options -   (, path )
 */
export function setCookie(name, value, options = {}) {
    const cookieOptions = {
        path: '/',
        SameSite: 'Strict',
        ...options
    };

    let cookieString = `${name}=${encodeURIComponent(value)}`;

    if (cookieOptions.expires) {
        if (typeof cookieOptions.expires === 'number') {
            const date = new Date();
            date.setTime(date.getTime() + cookieOptions.expires * 24 * 60 * 60 * 1000);
            cookieOptions.expires = date;
        }
        cookieString += `; expires=${cookieOptions.expires.toUTCString()}`;
    }

    if (cookieOptions.path) {
        cookieString += `; path=${cookieOptions.path}`;
    }

    if (cookieOptions.SameSite) {
        cookieString += `; SameSite=${cookieOptions.SameSite}`;
    }

    if (cookieOptions.secure) {
        cookieString += '; Secure';
    }

    document.cookie = cookieString;
}

/**
 *   
 * @param {string} name -  
 * @returns {string|null}    null
 */
export function getCookie(name) {
    const value = `; ${document.cookie}`;
    const parts = value.split(`; ${name}=`);
    if (parts.length === 2) return decodeURIComponent(parts.pop().split(';').shift());
    return null;
}

/**
 *   
 * @param {string} name -   
 */
export function deleteCookie(name) {
    document.cookie = `${name}=; path=/; expires=Thu, 01 Jan 1970 00:00:01 GMT`;
}

/**
 *    
 * @param {string} name -  
 * @param {number} value -   
 * @param {Object} options -   (, path )
 */
export function setNumberCookie(name, value, options = {}) {
    if (typeof value !== 'number') {
        console.warn(`setNumberCookie   .  : ${typeof value}`);
        return;
    }
    setCookie(name, value.toString(), options);
}

/**
 *    
 * @param {string} name -  
 * @returns {number|null}     null
 */
export function getNumberCookie(name) {
    const value = getCookie(name);
    if (value === null) return null;

    const number = Number(value);
    return isNaN(number) ? null : number;
}

/**
 *    
 * @param {string} name -  
 * @param {boolean} value -   
 * @param {Object} options -   (, path )
 */
export function setBooleanCookie(name, value, options = {}) {
    setCookie(name, value ? 'true' : 'false', options);
}

/**
 *    
 * @param {string} name -  
 * @returns {boolean|null}     null
 */
export function getBooleanCookie(name) {
    const value = getCookie(name);
    if (value === null) return null;
    return value === 'true';
}

/**
 *     ( )
 * @param {string} name -  
 * @param {Date|number} value -   (Date   )
 * @param {Object} options -   (, path )
 */
export function setDateCookie(name, value, options = {}) {
    let timestamp = new Date(value).getTime();
    if (isNaN(timestamp)) {
        if (value instanceof Date) {
            timestamp = value.getTime();
        } else if (typeof value === 'number') {
            timestamp = value.getTime();
        } else {
            console.warn(`setDateCookie  Date  () .  : ${typeof value}`);
            return;
        }
    }

    setCookie(name, timestamp.toString(), options);
}

/**
 *     ( )
 * @param {string} name -  
 * @returns {number|null}   null
 */
export function getDateCookie(name) {
    const value = getCookie(name);
    if (value === null) return null;

    const timestamp = Number(value);
    if (isNaN(timestamp)) return null;

    return new Date(timestamp);
} 