import ReceiverInfo from './ReceiverInfo';

/**
 *     
 */
class SendRequestModel {
    /**
     *   
     * @param {string} options.gubun -  
     * @param {Array<ReceiverInfo>} options.receiverList -   
     */
    constructor({
        gubun = '',
        receiverList = []
    } = {}) {
        /**
         *  
         * @type {string}
         */
        this.gubun = gubun;

        /**
         *   
         * @type {Array<ReceiverInfo>}
         */
        this.receiverList = receiverList;
    }

    /**
     *   
     * @param {ReceiverInfo} receiver -   
     */
    addReceiver(receiver) {
        if (receiver instanceof ReceiverInfo) {
            this.receiverList.push(receiver);
        } else {
            throw new Error('receiver ReceiverInfo  .');
        }
    }

    /**
     * JSON   
     * @returns {Object} -  
     */
    toJSON() {
        return {
            gubun: this.gubun,
            receiverList: this.receiverList.map(receiver => receiver.toJSON())
        };
    }

    /**
     *    
     * @returns {Object} - API    
     */
    toRequestBody() {
        return JSON.stringify(this.toJSON());
    }

    /**
     * FormData   ( )
     * @returns {FormData} -      
     */
    toFormData() {
        const formData = new FormData();
        formData.append('gubun', this.gubun);

        // ReceiverList JSON   
        formData.append('receiverList', JSON.stringify(this.receiverList.map(receiver => receiver.toJSON())));

        return formData;
    }
}

export default SendRequestModel; 