import { api } from '../utils/api';

/**
 *    API 
 */
const API_BASE_URL = import.meta.env.VITE_BASE_API_URL;

/**
 *    API
 * @param {string} language -  
 * @returns {Promise} - API  
 */
export async function getVisitReasons(language) {
    try {
        const response = await fetch(`${API_BASE_URL}/Visit/VisitReason?lan=${language}`, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            }
        });
        if (!response.ok) {
            throw new Error('API  ');
        }
        return await response.json();
    } catch (error) {
        console.error('    :', error);
        throw error;
    }
}

/**
 *    API
 * @param {Object} employeeData -  
 * @see employeeData.name -  
 * @returns {Promise} - API  
 */
export async function verifyEmployee(employeeData) {
    try {
        const response = await fetch(`${API_BASE_URL}/Visit/EmployeeByName?name=${employeeData.name}`, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            }
        });
        if (!response.ok) {
            throw new Error('API  ');
        }
        return await response.json();
    } catch (error) {
        console.error('    :', error);
        throw error;
    }
}

/**
 *   API
 * @param {Object} data -   
 * @see data.employeePid -  PID
 * @see data.employeeName -  
 * @see data.visitorName -  
 * @see data.visitorCompany - 
 * @see data.visitorContact -  
 * @see data.visitorEmail -  
 * @see data.visitReasonId -   ID
 * @see data.visitPurpose -  
 * @see data.visitDate -  
 * @see data.visitTime -  
 * @see data.visitEndDate -   
 * @see data.visitEndTime -   
 * @see data.visitorCarNumber -   
 * @returns {Promise} - API  
 */
export async function submitReservation(data) {
    const response = await fetch(`${API_BASE_URL}/Visit/VisitReserve`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(data),
    });
    if (!response.ok) throw new Error('  API  ');
    return await response.json();
}

/**
 *      API
 * @param {Object} visitorData -  
 * @see visitorData.visitorName -  
 * @see visitorData.visitorContact -  
 * @returns {Promise} - API   (isSuccess, needEducation )
 */
export async function checkEducationVideo(visitorData) {
    try {
        const response = await fetch(`${API_BASE_URL}/Visit/CheckEducation`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(visitorData)
        });

        if (!response.ok) {
            throw new Error('   API  ');
        }

        return await response.json();
    } catch (error) {
        console.error('    :', error);
        throw error;
    }
}

/**
 *    API
 * @returns {Promise} - API  
 */
export async function saveEducationCompletion() {
    try {
        const response = await api.post('/Visit/EducationCompletion');
        if (!response.ok) {
            throw new Error('   API  ');
        }
        return await response.json();
    } catch (error) {
        console.error('    :', error);
        throw error;
    }
}